.end {
  height: 100vh;
  background: white;
  font-family: FZYaoti;
}
.end .container {
  padding-top: 30vh;
  padding-left: 20vh;
  padding-right: 20vh;
  text-align: center;
  height: 50vh;
}
.end .handle-button {
  text-align: center;
}
.end .handle-button button {
  color: rgba(0, 0, 0, 0.438);
  border: transparent;
  background: transparent;
}
.end .handle-button button:hover {
  color: rgb(0, 0, 0);
}
